ks-cart-spinner {
    display: inline-flex;
    text-align: center;
}

ks-cart-spinner input {
    appearance: none;
    border: 1px solid #d9d9d9;

    width: 40px !important;
    height: 30px;
    box-sizing: border-box;
    
    margin: 0 !important;
    font-size: 14px !important;
    text-align: center !important;
}

ks-cart-spinner button {
    appearance: none;
    border: 1px solid #d9d9d9;
    
    height: 30px;
    width: 30px;
}

ks-cart-spinner button:first-child {
    border-right: none;
}

ks-cart-spinner button:last-child {
    border-left: none;
}

@media only screen and (max-width: 640px) {
    ks-cart-spinner input {
        width: 50px;
        height: 35px;
    }
    
    ks-cart-spinner button {
        height: 35px;
        width: 35px;
    }
}