import { Component, h, Prop, State, Method } from '@stencil/core';
import priceFormat from '../../../global/functions/price';
import * as cartFunctions from "../../../global/functions/cart";

@Component({
  tag: 'ks-cart-product',
  styleUrl: 'cartProduct.css'
})
export class CartProduct {
  @Prop({reflect: true}) productId: string;
  @Prop() name: string;
  @Prop() img: string;
  @Prop() link: string;
  @Prop({reflect: true}) small: boolean;
  @Prop() price: number;
  @Prop() shippingTime: string = "";
  @Prop() countUnit: string = "";

  @Prop({reflect: true}) amount: number = 1;
  @Prop() maxAmount: number;

  @Prop({reflect: true}) removable: boolean = false;
  @State() loading: boolean = false;

  onRemoveHandler() {
    this.loading = true;
    cartFunctions.removeProduct(this.productId);
  }

  @Method() async ResetLoading() {
      this.loading = false;
  }

  render() {
    const price = priceFormat(this.price);

    return[
        <a class="product-image" href={this.link}>
            <ks-img3 fit='contain' image={this.img} width={180} height={180} alt="zdjęcie produktu" />
        </a>,

        <div class="description">
            <a class="name" href={this.link} innerHTML={this.name} aria-label={this.name.replace(/<\/?[^>]+(>|$)/g, "") + ` - ${price} - ${this.shippingTime}`}></a>

            <div class="price">{price}</div>
            <div class="shipping">{this.shippingTime}</div>

            <div class="count">
                {this.removable ?
                    <ks-cart-spinner product={this.productId} value={this.amount} max={this.maxAmount} /> :
                    `${this.amount} ${this.countUnit}`
                }
            </div>

            <div class="remove-container">
                {this.removable ? this.loading ?
                    <ks-loader/> :
                    <button class="remove" onClick={()=>this.onRemoveHandler()} aria-label="Usuń produkt">
                        <ks-icon name="x" aria-hidden />
                    </button>
                : null}
            </div>
        </div>
    ];
  }
}
