ks-cart-product {
    display: flex;
    align-items: center;
    font-size: 16px;

    border-bottom: #ededed 1px solid;
}

ks-cart-product:last-child {
    border-bottom: none;
}

ks-cart-product .description {
    display: grid; 

    grid-template-columns: 1fr 110px 100px 50px; 
    grid-template-rows: auto auto; 
    gap: 0px 5px; 
    grid-template-areas: 
        "name     price count remove"
        "shipping price count remove";

    align-items: center;
    justify-content: center;
    line-height: normal;

    flex: 1;
    padding: 10px 0px;
}

ks-cart-product:not([removable]) .description {
    grid-template-columns: 1fr 110px 100px;
    grid-template-areas: 
        "name     price count"
        "shipping price count";
}

ks-cart-product:not([removable]) .remove-container {
    display: none;
}

ks-cart-product .name {
    grid-area: name;

    color: inherit !important;
    text-decoration: none !important;
}

ks-cart-product .price {
    grid-area: price;
    font-family: var(--font-emphasis);
    text-align: center;
}

ks-cart-product .count {
    grid-area: count;
    text-align: center;
}

ks-cart-product .remove-container {
    grid-area: remove;
}

ks-cart-product .shipping {
    grid-area: shipping;
    font-family: var(--font-emphasis);
    font-weight: 700;
}

ks-cart-product .remove {
    appearance: none;
    background-color: transparent;
    border: #d9d9d9 solid 1px;
    border-radius: 50px;
    width: 40px;
    height: 40px;
    margin: 5px;
}

ks-cart-product .product-image {
    width: 90px;
    height: 90px;
    max-width: 90px;
    max-height: 90px;
    padding: 15px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
}


@media only screen and (max-width: 860px) {
    ks-cart-product .description {
        grid-template-rows: auto auto; 
        grid-template-columns: auto auto 1fr; 

        gap: 5px 5px; 
        grid-template-areas: 
            "name   name     name  remove"
            "price  shipping count remove";
    }

    ks-cart-product:not([removable]) .description {
        grid-template-columns: auto auto 1fr;
        gap: 5px 10px;
        grid-template-areas: 
            "name     name name"
            "shipping price count";
    }

    ks-cart-product:not([removable]) .count {
        text-align: left !important;
    }

    ks-cart-product[removable] .count {
        text-align: left !important;
        margin-left: 10px;
    }
    
    ks-cart-product .price {
        text-align: center;
        font-weight: 700;
        color: #e21334;
    }

    ks-cart-product .name {
        margin-bottom: -5px;
    }
}

@media only screen and (max-width: 640px) {
    ks-cart-product:not([small]) {
        flex-direction: column;
        position: relative;
    }

    ks-cart-product:not([small]) .description {
        grid-template-rows: auto auto auto auto auto; 
        grid-template-columns: auto auto; 

        gap: 8px; 
        grid-template-areas: 
            "name   name"
            "price  price"
            "count  count"
            "shipping shipping"
            "remove remove";

        text-align: center;
        justify-items: stretch;
    }

    ks-cart-product:not([small]):not([removable]) .description {
        grid-template-rows: auto auto; 
        grid-template-columns: 1fr auto 1fr;

        grid-template-areas: 
            "name   name     name"
            "price  shipping count";
    }

    ks-cart-product[removable] .count {
        text-align: center !important;
        margin-left: 0px;
    }

    ks-cart-product:not([small]) .remove-container {
        position: absolute;
        text-align: right;
        top: 5px;
        right: 0px;
    }

    ks-cart-product:not([small]) .product-image {
        width: 100%;
        height: 100%;
        max-width: 180px;
        max-height: 180px;
    }

    ks-cart-product[small] {
        font-size: 14px;
    }
}

@media only screen and (max-width: 420px) {
    ks-cart-product[small] .product-image {
        max-width: 60px;
        max-height: 60px;
        padding: 10px 10px 10px 0px;
    }
}