ks-cart-product-heading {
    display: flex;
    align-items: center;
    text-align: center;
    font-size: 18px;

    border-bottom: #ededed 1px solid;
    padding-bottom: 10px;

    font-family: var(--font-emphasis);
    font-weight: 700;
}

ks-cart-product-heading .names {
    text-align: left;
    width: 100%;
}

ks-cart-product-heading .prices {
    width: 110px;
    flex: 1 0 auto;
}

ks-cart-product-heading .count {
    width: 110px;
    flex: 1 0 auto;
}

ks-cart-product-heading .remove {
    width: 50px;
    flex: 1 0 auto;
}


ks-cart-product-heading .remove {
    width: 50px;
    flex: 1 0 auto;
}

@media only screen and (max-width: 860px) {
    ks-cart-product-heading .prices {
        display: none;
    }
    
    ks-cart-product-heading .count {
        display: none;
    }
}