import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-cart-product-heading',
  styleUrl: 'cartProductHeading.css'
})
export class CartProductHeading {
  @Element() root: HTMLElement;
  @Prop() removable: boolean;
  
  @Prop() productTableNames: string = "";
  @Prop() productTablePrices: string = "";
  @Prop() productTableCount: string = "";
  @Prop() productTableRemove: string = "";

  render() {
    return[
        <div class="names">{this.productTableNames}</div>,

        <div class="prices">{this.productTablePrices}</div>,
        <div class="count">{this.productTableCount}</div>,

        this.removable ? <div class="remove">{this.productTableRemove}</div> : null 
    ];
  }
}
