import { Component, h, Prop, Element } from '@stencil/core';
import * as cartFunctions from "../../../global/functions/cart";

@Component({
  tag: 'ks-cart-spinner',
  styleUrl: 'cartSpinner.css'
})
export class CartSpinner {
  @Element() root: HTMLElement;

  @Prop() product: string;
  @Prop() name: string;
  @Prop({ mutable: true, reflect: true }) value: number = 1;
  @Prop() max: number;

  Increment() {
    if(typeof this.max === "undefined" || this.value < this.max) {
      this.value += 1;

      this.onChangedHandler(this.value);
    }
  }

  Decrement() {
    if(this.value > 1) {
      this.value -= 1;

      this.onChangedHandler(this.value);
    }
  }

  Change(e: Event) {
    const input = e.target as HTMLInputElement;

    const parsed = parseInt(input.value);

    if(!isNaN(parsed) && parsed > 0 && (typeof this.max === "undefined"  || parsed <= this.max))
      this.value = parsed;

    else if (parsed > this.max ) {
      this.value = this.max;
      input.value = this.max.toString();
    }

    else {
      this.value = 1;
      input.value = "1";
    }

    this.onChangedHandler(this.value);
  }

  onChangedHandler(value: any) {
    cartFunctions.productCount(this.product, value).then(maxCount => {
      setTimeout(() => {
        if(!maxCount) return;
        this.max = maxCount;

        const input = this.root.querySelector('input') as HTMLInputElement;
        const parsed = parseInt(input.value);

        if (parsed > this.max ) {
          this.value = this.max;
          input.value = this.max.toString();
        }
      }, 200);
    });
  }

  timeout: number;

  render() {
    return [
        <button onClick={()=>this.Decrement()} disabled={this.value <= 1}>-</button>,
        <input type="text" maxlength="3" value={this.value} name={this.name} aria-label="ilość"
                onChange={(e)=>this.Change(e)}
        />,
        <button onClick={()=>this.Increment()} disabled={(this.value+1) > this.max}>+</button>
    ];
  }
}
